#high-scores{

    h1{
        color: gold; 
    }

    h2{
        color:#ab0e00;
    }

   .score-table{
       display: flex;
       justify-content: space-around;
       width: 100%;

       margin-top: 20px;
       margin-bottom: 30px;
   }

   .column{
       display: flex;
       flex-direction: column;
       align-items: center;
       width: 50%;
   }
    
   @media (max-width: 600px) {
        margin-top: 30px;
   }
}